export class PageInfo<T> {

  [key: string]: any;

  constructor(
    public pageNum?: number, // å½åé¡µç ï¼ä¸currentåä½ï¼æ¨èç¨pageNumï¼
    public pageSize?: number, // æ¯é¡µè¡æ°ï¼ä¸sizeåä½ï¼æ¨èç¨pageSizeï¼
    public pages?: number, // æ»é¡µæ°
    public list?: Array<T>, // å®éæ°æ®éå
    public total?: number, // æ»è¡æ°

    public current?: number, // å½åé¡µç 
    public size?: number, // å½åé¡µå®éå¤å°è¡
    public totalPages?: number, // æ»é¡µæ°
    public totalRows?: number, // æ»è¡æ°
    public rows?: Array<T>, // å®éæ°æ®

    public endRow?: number, // å½åé¡µç»æè¡å·ï¼åºäº1ä¸ºç¬¬ä¸è¡
    public startRow?: number, // å½åé¡µå¼å§è¡å·ï¼åºäº1ä¸ºç¬¬ä¸è¡

    public resultCode?: string,
    public resultMsg?: string,
  ) {
    if (this.pageNum) {
      this.current = this.pageNum;
    }
    if (this.pageSize) {
      this.size = this.size;
    }
    // å½åé¡µç 
    this.pageNum = !pageNum ? 1 : pageNum;
    this.pageSize = !pageSize ? 20 : pageSize;
    this.size = !size ? 20 : size;
    this.totalRows = !totalRows ? 0 : totalRows;
    this.total = !total ? 0 : total;
    this.list = !list ? [] : list;
    this.rows = !rows ? [] : rows;
  }

  get offset(): number {
    return this.pageNum - 1;
  }

  set offset(offset: number) {
    this.pageNum = offset + 1;
  }

  public static fromJson<T>(json: object): PageInfo<T> {
    if (!json) {
      return new PageInfo<T>();
    }
    return Object.setPrototypeOf(json, PageInfo.prototype);
  }
}
