import {Injectable} from '@angular/core';
import {AbstractControl, ValidatorFn} from '@angular/forms';
import {FormValidatorModel} from '../models/form-validator.model';
import {RegexpUtils} from 'great-jsutils';

@Injectable({
  providedIn: 'root'
})
export class FormValidatorService {

  constructor() {
  }

  /**
   * éªè¯æ¯å¦å¨ä¸ºä¸­æ
   * @param model:
   */
  chinese(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^[\u4e00-\u9fa5]+$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (!control.value) {
        return null;
      }
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * å­æ¯æ°å­
   * @param model:
   */
  alphanumeric(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^[A-Za-z0-9]+$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * æ ¹æ®è¾å¥æ­£åæ ¡éª
   * @param model:
   */
  regex(model: FormValidatorModel): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = model.value;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * èº«ä»½è¯
   * @param model:
   */
  identityCard(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * é®ç®±
   * @param model:
   */
  email(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * ææºå·
   * @param model:
   */
  mobile(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^[1][3,4,5,7,8][0-9]{9}$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * url
   * @param model:
   */
  url(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^((ht|f)tps?):\/\/([\w\-]+(\.[\w\-]+)*\/)*[\w\-]+(\.[\w\-]+)*\/?(\?([\w\-\.,@?^=%&:\/~\+#]*)+)?/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (control.value && model.trimLR) {
        control.setValue(control.value.trim());
      }
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * æ´æ°
   * @param model:
   */
  integer(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    const regExp: RegExp = /^\d+$/;
    return (control: AbstractControl): { [key: string]: any } | null => {
      const result = regExp.test(control.value);
      return this.buildReturnResult(result, model, control);
    };
  }

  /**
   * æ°å­éªè¯
   * @param model:
   */
  decimal(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    return (control: AbstractControl): { [key: string]: any } | null => {
      let options = {};
      if (model.scale) {
        options = {scale: model.scale};
      } else if (model.maxScale) {
        options = {scale: model.maxScale};
      }
      if (model.min) {
        options = {...options, min: model.min};
      }
      if (model.max) {
        options = {...options, max: model.max};
      }
      const result = RegexpUtils.isNumber(control.value, options);
      return this.buildReturnResult(result, model, control);
    };
  }

  byteLength(model?: FormValidatorModel | any): ValidatorFn {
    model = FormValidatorModel.init(model);
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (!control.value) {
        return null;
      }
      let options = {};
      if (model.minLength) {
        options = {minLength: model.minLength};
      } else if (model.maxLength) {
        options = {maxLength: model.maxLength};
      }
      if (control.value && model.trimLR) {
        control.setValue(control.value.trim());
      }
      const result = RegexpUtils.byteLength(control.value, options);
      return this.buildReturnResult(result, model, control);
    };
  }

  private buildReturnResult(regExpResult, model: FormValidatorModel, control) {
    if (model.maxLength && regExpResult) {
      regExpResult = control.value.length <= model.maxLength;
    }
    if (model.minLength && regExpResult) {
      regExpResult = control.value.length >= model.minLength;
    }
    const defaultModel = model.defaultValue();
    if (regExpResult) {
      return null;
    } else {
      const result = {};
      const key = !!model.key ? model.key : defaultModel.key;
      const errorMsgKey = !!model.errorMsgKey ? model.errorMsgKey : defaultModel.errorMsgKey;
      const errorMsg = !!model.errorMsg ? model.errorMsg : defaultModel.errorMsg;
      result[key] = {value: control.value};
      if (model.errorMsgKey) {
        result[key][errorMsgKey] = errorMsg;
      } else {
        result[key][errorMsgKey] = errorMsg;
      }
      return result;
    }
  }
}
